* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --w: clamp(1px, 0.2vw, 3px);
}
html, body {
  height: 100%;
}

body {
  background-color: black;
  color: #eee;
  font-family: Helvetica, Arial;
  font-size: 3vmin;
  overflow: hidden;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.hidden {
  display: none;
}


.background {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-size: cover;
  background-position: center center;
  /*filter: blur(8em) opacity(0.6);*/
  position: absolute;
}

.main-wrapper {
  top:0;
  bottom:0;
  align-items: center;
  display: flex;
  height: 100%;
  margin: auto;
  justify-content: center;
  position: absolute;
  width: 80%;
  z-index: 1;
}

.container {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100%;
}


.now-playing__name {
  font-size: 1.5em;
  margin-bottom: 0.2em;
}

.now-playing__artist {
  margin-bottom: 1em;
}

.now-playing__status {
  margin-bottom: 1em;
}

.now-playing__cover {
  position: absolute;
  height: 30vw;
  width: 30vw;
  z-index: 1;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  margin: auto;
  backdrop-filter: blur(6px);
  border-radius: 4vw;
  opacity: 0.5;
  animation: trackPicHover 2s ease-in-out alternate infinite;
}


@keyframes trackPicHover {
  0% {
    transform: translateY(20px);
    /* box-shadow: inset 0px 0px 30px 10px rgba(255,255,255, 0.4); */
    
  }

  100% {
    transform: translateY(-20px);
    
    /* box-shadow: inset 0px 0px 80px 20px rgba(255,255,255, 0.5); */
  }
}

.player-buttons {
  display: flex;
  align-items: center;
}

.btn-spotify {
  color: white;
  padding-left: 10px;
  padding-right: 10px;
}

.bubblesContainer {
  top: 0;
  left: 0;
  position: absolute;
  z-index: -1;
  width: 100vw;
  height: 100vh;
}

.bubble {
  position: absolute;
  background-color: orange;
  border-radius: 50%;
  width: var(--w);
  height: var(--w);
  box-shadow: 0 0 0 calc(var(--w)/4) rgba(255, 0, 0, 0.2),
    0 0 calc(var(--w)* 2) red,
    0 0 calc(var(--w)* 4) red;
    animation: animateBubbles 5s linear infinite;
    animation-delay: -100s;
}

@keyframes animateBubbles {
  0% {
      left: 100vw;
  }

  100% {
      left: -10vw;
  }
}

.player {
  
  position: absolute;
  height: 8%;
  width: 10%;
  z-index: 1;
  bottom: 20px;
  left: 0;
  right: 0;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255,255,255,0.15);
  box-shadow:inset 0px 0px 10px 5px rgba(255,255,255, 0.2);
  border-radius: 25px;
  backdrop-filter: blur(3px);
  display: none;
}